<template>
  <v-card class="ma-md-6 ma-sm-3">
    <v-card-title class="py-2">
      <v-icon class="mr-3">mdi-account-setting</v-icon>
      Pengaturan Akun
    </v-card-title>
    <v-divider></v-divider>
    <v-form v-model="valid" ref="form">
      <v-card-text>
        <v-text-field
          v-model="dataUnit.nama"
          label="Username"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="password_lama"
          label="Password lama"
          outlined
          dense
          :rules="password_lama===''?[]:passwordLamaRule"
          autocomplete="off"
          :append-icon="showpasswordLama ? 'mdi-eye' : 'mdi-eye-off'"
          :type="showpasswordLama ? 'text' : 'password'"
          @click:append="showpasswordLama = !showpasswordLama"
        ></v-text-field>
        <v-text-field
          v-model="password_baru"
          label="Password baru"
          outlined
          dense
          autocomplete="off"
          :append-icon="showpasswordBaru ? 'mdi-eye' : 'mdi-eye-off'"
          :type="showpasswordBaru ? 'text' : 'password'"
          @click:append="showpasswordBaru = !showpasswordBaru"
        ></v-text-field>
        <v-text-field
          :disabled="password_baru===''?true:false"
          v-model="password_konfirmasi"
          label="Konfirmasi password"
          outlined
          dense
          :rules="password_baru===''?[]:konfirmasiPassword"
          autocomplete="off"
          :type="showpasswordBaru ? 'text' : 'password'"
        ></v-text-field>
        <v-text-field
          v-model="dataUnit.nohp"
          label="No Hp/WA"
          outlined
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>

        <Notif :msg="msg" :status="status" @visible="visible" />
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          @click="$router.go(-1)"
          text>
          kembali
        </v-btn>
        <v-btn
          :disabled="!valid"
          color="primary"
          :loading="isLoading"
          @click.prevent="validate"
          type="submit"
          depressed>
          simpan
        </v-btn>
      </v-card-actions>
    </v-form>
  </v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    data () {
      return {
        valid: true,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil disimpan',
          error: 'Data ggal disimpan',
          visible: false
        },
        password_lama: '',
        password_baru: '',
        password_konfirmasi: '',
        showpasswordLama: false,
        showpasswordBaru: false
      }
    },

    components: {Notif},

    created() {
      this.$store.dispatch('dataAdmin/getDataUnit', window.localStorage.getItem('unit_id'))
      this.$store.dispatch('dataAdmin/updateIsLoading', true)
    },

    computed: {
      ...mapState('dataAdmin', ['dataUnit', 'isLoading', 'status']),
      passwordLamaRule() {
        return [ v => (v === this.dataUnit.password) || 'Password lama tidak cocok' ]
      },
      konfirmasiPassword() {
        return [ 
          v => !!v || 'Data harus diisi',
          v => (v === this.password_baru) || 'Password tidak cocok' 
        ]
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          let data = {
            id: this.dataUnit.id,
            nama: this.dataUnit.nama,
            nohp: this.dataUnit.nohp,            
          }

          if (this.password_konfirmasi !== '') {
            data.password = this.password_konfirmasi
          }

          this.$store.dispatch('dataAdmin/putDataUnit', data)
          this.$store.dispatch('dataAdmin/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataAdmin/updateStatus', null)
      }
    }
  }
</script>
